.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

select {
  display: block;
  margin: 10px 10px 10px 0px;
}

.title {
  padding: 2%;
  margin: 20px;
  border-radius: 5px;
  background-color: antiquewhite;
  font-family: Roboto;
  font-size: x-large;
  color: coral;
}

.container {
  border: 2px solid lightcoral;
  padding: 2%;
  margin: 20px;
  border-radius: 36px;
}

.btn-primary,
.btn-primary:active,
.btn-primary:visited {
  background-color: coral !important;
  padding: 2px;
  border-radius: 7px;
}

.btn-primary:hover {
  background-color: lightcoral !important;
}

#number-input {
  margin: 10px 10px 10px 0px;
  color: white;
  border-radius: 5px;
}
